import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state={
    notes:[],
    activeNote:{}
}

const mutations={
    ADD_NOTE(state){
        const newNote={
            text:"new text",
            favor:false
        }
        state.notes.push(newNote);
        state.activeNote=newNote;
    },
    REMOVE_NOTE(state){
        for(var i=0;i<state.notes.length;i++){
            if(state.notes[i] == state.activeNote){
                state.notes.splice(i,1);
                state.activeNote=state.notes[0];
            }
        }
    },
    DEL_NOTE(state,note){
        for(var i=0;i<state.notes.length;i++){
            if(state.notes[i] == note){
                state.notes.splice(i,1);
                state.activeNote=state.notes[0];//bug:点垃圾桶删除后，不能出现激活状态的笔记
            }
        }
    },
    EDIT_NOTE(state,text){
        state.activeNote.text=text;
    },
    TOGGLE_FAVOR(state){
        state.activeNote.favor = !state.activeNote.favor;
    },
    SET_ACTIVE_NOTE(state,note){
        state.activeNote=note;
    }
}

const actions={
    addNote({commit}){
        commit('ADD_NOTE')
    },
    removeNote({commit}){
        commit('REMOVE_NOTE')
    },
    delNote({commit},note){
        commit('DEL_NOTE',note)
    },
    editNote({commit},text){
        commit('EDIT_NOTE',text)
    },
    toggleFavor({commit}){
        commit('TOGGLE_FAVOR')
    },
    setActiveNote({commit},note){
        commit('SET_ACTIVE_NOTE',note)
    }
}

const getters={
    notes:state=>state.notes,
    activeNote:state=>state.activeNote
}

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters
})


