<template>
    <div id="editor">
        <textarea :value="activeNoteText" v-on:input="editNote" class="form-control"></textarea>
    </div>
    <!-- <div id="edi">
        <div id="editor"></div>
        <textarea id="text1" :value="activeNoteText" v-on:input="editNote" class="form-control"></textarea>
    </div> -->
</template>

<script>

export default {
   computed:{
     activeNoteText(){
       return this.$store.getters.activeNote.text
     }
   },
   methods:{
     editNote(e){
         this.$store.dispatch('editNote',e.target.value)
     }
   },
  /* mounted(){
      var E = window.wangEditor
        var editor = new E('#editor')
        var text1 = document.querySelector('#text1')
        for(var i=0;i<this.$store.getters.notes.length;i++){
            if(this.$store.getters.notes[i] == this.$store.getters.activeNote){
                editor.customConfig.onchange = function (text) {
                    // 监控变化，同步更新到 textarea
                    text1.value=text,
                    text=text1.value
                }
                editor.create()
                // 初始化 textarea 的值
                text1.value=editor.txt.text()
            }else{
                editor.$textElem.attr('contenteditable', false)
            }
        }
    } */
}
</script>

<style scoped>
#editor{
    height:100%;
    margin-left:380px;
}
#editor textarea {
    height:100%;
    border: 0;
    border-radius: 0;
}
</style>



