</template>

<script>
import toolBar from './components/ToolBar.vue'
import notesList from './components/NotesList.vue'
import noteEditor from './components/NoteEditor.vue'

export default {
  name: 'App',
  components: {
      toolBar,
      notesList,
      noteEditor
  }
}
</script>

<style scoped>
html #app{
  height: 100%;
}
body{
    margin: 0;
    padding: 0;
    height: 100%;
    max-height: 100%;
    border: 0;
    position: relative;
}
</style>



