<template>
  <div id="left">
      <i class="glyphicon glyphicon-plus" @click="add()"></i>
      <i class="glyphicon glyphicon-star" @click="fav()" v-bind:class="{change:activeNote.favor}"></i>
      <i class="glyphicon glyphicon-remove" @click="remove()"></i>
  </div>
</template>

<script>
export default {
  computed:{
    activeNote(){
      return this.$store.getters.activeNote
    }
  },
  methods:{
      add(){
          this.$store.dispatch('addNote')
      },
      fav(){
          this.$store.dispatch('toggleFavor')
      },
      remove(){
          this.$store.dispatch('removeNote')
      }
  }
}
</script>

<style scoped>
#left{
    width: 80px;
    height: 100%;
    float: left;
    background: #30414D;
    padding: 35px 25px 25px 25px;
    color: #767676;
}
#left i{
    cursor: pointer;
    font-size: 30px;
    margin-bottom: 35px;
    opacity: 0.8;
    transition: opacity 0.5s ease;
}
#left i:hover{
    opacity: 1;
}
.change{
    color: #F7AE4F;
}
</style>



