<template>
    <div id="list">
        <div id="header">
            <h2>笔记</h2>
            <div class="btn-group btn-group-justified" role="group" aria-label="...">
                <div class="btn-group" role="group">
                    <button type="button" class="btn btn-default" @click="show='all'" :class="{active:show=='all'}">所有笔记</button>
                </div>
                <div class="btn-group" role="group">
                    <button type="button" class="btn btn-default" @click="show='favor'" :class="{active:show=='favor'}">收藏笔记</button>
                </div>                  
            </div>
        </div>
                  
        <div id="container">
            <div class="list-group">
                <a v-for="item in notes" class="list-group-item clearfix" @click="active(item)" 
                :class="{active:activeNote==item}" href="#">
                    {{item.text}}
                    <span class="pull-right">
                        <button class="btn btn-xs btn-info">
                            <span class="glyphicon glyphicon-edit"></span>
                        </button>
                        <button class="btn btn-xs btn-warning" @click="del(item)">
                            <span class="glyphicon glyphicon-trash"></span>
                        </button>
                    </span>
                </a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data(){
      return {
          show:'all'
      }
  },
  computed:{
      notes(){
          if(this.show=='all'){
              return this.$store.getters.notes
          }else if(this.show=='favor'){
              return this.$store.getters.notes.filter(note=>note.favor)
          }
      },
      activeNote(){
          return this.$store.getters.activeNote
      }
  },
  methods:{
      active(note){
          this.$store.dispatch('setActiveNote',note)
      },
      del(note){
          this.$store.dispatch('delNote',note)
      }
  }
}
</script>

<style scoped>
#list {
  float: left;
  width: 300px;
  height: 100%;
  background-color: #F5F5F5;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
}

#header {
  padding: 5px 25px 25px 25px;
}

#header h2 {
  font-weight: 300;
  text-transform: uppercase;
  text-align: center;
  font-size: 22px;
  padding-bottom: 8px;
}

#list .container {
  height: calc(100% - 137px);
  max-height: calc(100% - 137px);
  overflow: auto;
  width: 100%;
  padding: 0;
}

#list .container .list-group-item {
  border: 0;
  border-radius: 0;
}

.list-group-item-heading {
  font-weight: 300;
  font-size: 15px;
}
</style>



